.PhoneDetails{
    position: relative;
    background-color: rgb(255, 255, 255);
    display: grid;
    grid-template-columns: 40% 60%;
    /* grid-template-rows: 10% 90%; */
    /* overflow: hidden; */
}

.PDsection1{
    display: grid;
    /* max-height: 200vh; */
    height: fit-content;
    grid-template-rows: 75% 25%;

}

.PDselectImage ul{
    /* height: 100%; */
    /* margin: 0px 10px 0px 10px; */
    width: 100%;
    padding: 0px;
    background-color: rgb(255, 255, 255);
    border: 1px dashed rgba(0,0,0,0.3);
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    
}

.PDselectImage li{
    list-style: none;
    padding: 5px;
    box-shadow: 0 5px 10px rgba(0,0,0,0.3);
    width: 40px;
    margin: 10px 10px 10px 10px;
    border-radius: 5px;
    cursor: pointer;
    /* transform: translate(-50%,-50%); */
}

.PDselectImage img{
    width:100%;
}

.PDdisplayImage{
    width: 100%;
    border-right: 1px dashed rgba(0,0,0,0.3);
    border-left: 1px dashed rgba(0,0,0,0.3);
    display: flex;
    justify-content: center;
    align-items: center;
}
.PDdisplayImage img{
    width: 80%;
}


.PDsection2{
    position: relative;
}

.PDheading{
    position: relative;
    background-color: #fff;
    padding: 20px;
    /* box-shadow: 0px 1px 5px 5px rgba(0,0,0,0.2); */
    text-align: center;
}

.PDbody{
    /* margin-top: 20px; */
    padding: 10px;
}

.PDbuttons{
    display: grid;
    grid-template-columns: 35% 35% 30%;
    grid-template-rows: 25% 25% 25% 25%;
    grid-gap: 5px;    
    height:150px;
}

.PDbuttons img{
    height: 100%;
}

.R-stars{
    margin-left: 15px ;
    display: flex;
    flex-direction: row;
    /* justify-content: center; */
    align-items: flex-start;
}

.R-stars .stars{
    background-color: rgb(0,3,158);
    color: #fff;
    padding: 2px 8px;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;

}

.R-stars .stars .bx{
    padding-left: 3px;
    font-size: 12px;
}

.R-stars h5{
    padding-left: 25px;
}

